@import './index.scss';
.ForecastHour-wrapper{
    position: relative;
    overflow-x: scroll;
    overflow-y: hidden;
    scrollbar-color: #ffffff #00000031;
    scrollbar-width: 1rem;
}
.ForecastHour-Container {
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    width: fit-content;
    margin: 0 auto;
}
.ForecastHour-wrapper::-webkit-scrollbar {
    width: 1em;
}
.ForecastHour-wrapper::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
}
.ForecastHour-wrapper::-webkit-scrollbar-thumb {
    background-image: linear-gradient(180deg, #f0f0f0 0%, #8c909b 99%);
    //box-shadow: inset 2px 2px 5px 0 rgba(#fff, 0.5);
    border-radius: .5rem;
}
.Hourly-Forecast-Wrapper {
    margin: .5rem;
}
.Weather-Icon-Wrapper-Forecast-Hour {
    width: 3rem;
    height: 3rem;
    margin-top: .5rem;
    fill: $white-main;
}
.Hourly-Forecast-Temp {
    font-size: 1.2rem;
}